.question {
    background-color: lightblue;
    width: 600px;
    font-size: 45px;
    text-align: center;
    margin-bottom: 50px;
    border-radius: 20px;
}
.answer {
    background-color: lightblue;
    width: 600px;
    font-size: 35px;
    text-align: center;
    margin-bottom: 20px;
    border-radius: 18px;
    
}
@media screen and (max-width: 768px) {
    .startGame {
        font-size: 50px;
        color: green;
        margin: 200px auto auto 130px;
        background-color: lightblue;
    }
    .wapper {
        width: 400px;
        font-size: 40px;
        border-radius: 30px;
    }
    .question {
        margin-top: 30%;
        background-color: lightblue;
    }
    .answers {
        width: 600px;
    }
    .answers > div {
        background-color: lightblue;
        text-align: center;
        margin-bottom: 20px;
        border-radius: 18px;
        font-size: 45px;
        
    }
}